<template>
  <div class='toast' v-show='isShow'>
    <div>{{message}}</div>
  </div>
</template>
<script>
    export default {
        name: 'Toast',
        data(){
            return {
                isShow:false,
                message:''
            }
        },
        methods:{
            show(message,time=2000){
                this.isShow = true  //先显示遮罩层
                this.message = message
                setTimeout(()=>{
                    this.isShow = false
                    this.message = ''
                },time)
            }
        }
        // props:{
        //     message:{
        //         type:String,
        //         default:''
        //     },
        //     isShow:{
        //         type:Boolean,
        //         default: false
        //     }
        // }
       
    }

</script>
<style lang="less" scoped>
  .toast {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 8px 10px;
    /*z-index: 999;*/
    color: #fff;
    background-color: rgba(0, 0, 0, .75);
  }
</style>
